import {
    HStack,
    Heading,
    Image,
    SimpleGrid,
    Text,
    VStack,
} from "@chakra-ui/react";

import ApniAutoImage from "../assets/images/apniauto.png";
import ApniBikeImage from "../assets/images/apnibike.png";
import ApniCabImage from "../assets/images/apnicab.png";
import ApniRentalImage from "../assets/images/apnirental.png";
import TextBanner from "./TextBanner";

function ServiceCard({ title,  image }) {
    return (
        <HStack p={[5]}>
            <Image src={image} alt={title} />
            <VStack>
                <Heading textTransform={"uppercase"}>{title}</Heading>
                
            </VStack>
        </HStack>
    );
}

function Services() {
    return (
        <>
            <TextBanner>Our Categories</TextBanner>
            <SimpleGrid
            my={12}
                columns={[1, 2]}
                justifyItems={"center"}
                spacing={[8, 10]}
                m={["2xl", "3xl"]}
            >
                <ServiceCard
                    title="Apni Auto"
                    image={ApniAutoImage}
                />
                <ServiceCard
                    title="Apni Bike"
                    image={ApniBikeImage}
                />
                <ServiceCard
                    title="Apni Cab"
                    image={ApniCabImage}
                />
                <ServiceCard
                    title="Apni Rental"
                    image={ApniRentalImage}
                />
            </SimpleGrid>
        </>
    );
}

export default Services;
