import {
    Box,
    Flex,
    HStack,
    Heading,
    Icon,
    IconButton,
    Link,
    Menu,
    MenuButton,
    MenuItem,
    MenuList,
    Stack,
    useColorModeValue,
} from "@chakra-ui/react";
import { CabIcon, MenuIcon } from "../assets/icons";

import { useBreakpointValue } from "@chakra-ui/media-query";
import { useState } from "react";

const NavLink = ({ children, link = "#" }) => (
    <Link
        px={2}
        py={1}
        rounded={"md"}
        fontWeight="bold"
        _hover={{
            color: useColorModeValue("green.500", "green.500"),
            opacity: 1,
            transform: "scale(1.3)",
        }}
        href={link}
        textTransform={"uppercase"}
    >
        {children}
    </Link>
);

export default function Navbar() {
    // const { colorMode, toggleColorMode } = useColorMode();
    const isMobile = useBreakpointValue({ base: true, md: false });
    const [navBg, setNavBg] = useState("transparent");

    window.addEventListener("scroll", () => {
        if (window.scrollY > window.innerHeight / 4) {
            setNavBg("#000000");
        } else {
            setNavBg("transparent");
        }
    });

    return (
        <>
            <Box
                bg={navBg}
                color={useColorModeValue("#ffffff")}
                className="sticky"
                px={4}
            >
                <Flex
                    h={16}
                    alignItems={"center"}
                    justifyContent={"space-between"}
                >
                    <HStack spacing="1" px={1} py={2}>
                        <Heading>Apni</Heading>
                        <Icon fontSize="4xl" as={CabIcon} mt="1" />
                        <Heading>Gaadi</Heading>
                    </HStack>

                    <Flex alignItems={"center"}>
                        {isMobile ? (
                            <Menu>
                                <MenuButton
                                    as={IconButton}
                                    icon={<MenuIcon />}
                                    variant={"ghost"}
                                    color={"white"}
                                    _clicked={{ bg: "transparent" }}
                                    _focus={{ bg: "transparent" }}
                                    _hover={{ bg: "transparent" }}
                                ></MenuButton>
                                <MenuList color="#000">
                                    <MenuItem>About Us</MenuItem>
                                    <MenuItem>Our Team</MenuItem>
                                    <MenuItem>Our Mission</MenuItem>
                                    <MenuItem>Our Vision</MenuItem>
                                    {/* <MenuItem onClick={toggleColorMode}>
                                        {colorMode === "light" ? (
                                            <MoonIcon />
                                        ) : (
                                            <SunIcon />
                                        )}
                                    </MenuItem> */}
                                </MenuList>
                            </Menu>
                        ) : (
                            <Stack direction={"row"} spacing={6}>
                                <NavLink>About Us</NavLink>
                                <NavLink>Our Team</NavLink>
                                <NavLink>Our Mission</NavLink>
                                <NavLink>Our Vision</NavLink>
                                <NavLink>Contact</NavLink>
                                {/* <Icon m={2} onClick={toggleColorMode} as={colorMode === "light" ? (
                                        MoonIcon
                                    ) : (
                                        SunIcon
                                    )}>
                                    
                                </Icon> */}
                            </Stack>
                        )}
                    </Flex>
                </Flex>
            </Box>
        </>
    );
}
