import { Box, Heading, Image, Stack, Text } from "@chakra-ui/react";

import TextBanner from "./TextBanner";
import image4 from "../assets/images/image-4.png";
import image5 from "../assets/images/image-5.png";
import image6 from "../assets/images/image-6.png";
import image7 from "../assets/images/image-7.png";
import image8 from "../assets/images/image-8.png";

function AboutSection({
    heading,
    image,
    children,
    imageProps,
    reverse = false,
}) {
    return (
        <>
            <Stack
                direction={["column", reverse ? "row-reverse" : "row"]}
                spacing="24px"
                align="center"
                justify="center"
                p={["24px", "48px"]}
            >
                <Box p={["28px", "56px"]} data-aos="zoom-in-up">
                    <Image {...imageProps} src={image} />
                </Box>
                <Box p={["28px", "56px"]} data-aos="zoom-in">
                    <Text fontSize={"lg"}>{children}</Text>
                </Box>
            </Stack>
        </>
    );
}

function About() {
    return (
        <>
            <TextBanner>What we are?</TextBanner>
            <AboutSection image={image4} reverse>
                Apni Gaadi is a pioneering transportation company that has
                revolutionized the way people travel. With our innovative
                methods and commitment to affordability, we have become a
                trusted name in the industry. At the core of Apni Gaadi's
                success is our customer-centric approach. We prioritize the
                needs and convenience of our passengers, ensuring a seamless and
                enjoyable travel experience. With a user-friendly mobile
                application, booking a ride has never been easier. Our app
                allows passengers to quickly select their destination, choose
                their preferred vehicle type, and track their ride in real-time.
                We have simplified the payment process by enabling in-app
                transactions, eliminating the hassle of cash payments.
            </AboutSection>
            <AboutSection image={image5}>
                Furthermore, we are proud to be the first to introduce sharing
                features in our platform. By enabling passengers to share rides
                with others heading in the same direction, we promote a greener
                and more sustainable mode of transportation. Our innovative
                sharing features not only reduce costs for passengers but also
                contribute to easing traffic congestion and minimizing
                environmental impact. Another aspect we take pride in is our
                commitment to ensuring fair earnings for our drivers. We
                understand that they are the backbone of our service, and their
                satisfaction is crucial. We provide competitive compensation,
                maximum cuts, and minimal platform fees, allowing our drivers to
                earn a fair income and build a successful career with us. We
                empower them with resources for professional growth, flexible
                working hours, and dedicated support, fostering a mutually
                beneficial relationship.
            </AboutSection>
            <AboutSection image={image6} reverse>
                Safety is paramount at Appni Gaadi. We have implemented
                stringent safety measures, including comprehensive background
                checks for drivers and regular vehicle inspections, to ensure
                the well-being of our passengers. We prioritize their security
                and peace of mind throughout their journey. One of the key
                features that sets us apart is our emphasis on affordability. We
                understand that transportation costs can be a significant burden
                for many individuals, so we strive to provide the most
                competitive prices in the market. With Appni Gaadi, passengers
                can enjoy high-quality rides at affordable rates, making us the
                go-to choice for budget-conscious travelers. In conclusion,
                Appni Gaadi is revolutionizing transportation by combining
                innovation, affordability, and a customer-centric approach. With
                our user-friendly app, competitive prices, sharing features,
                fair earnings for drivers, and unwavering commitment to safety,
                we are transforming the way people travel, making transportation
                accessible, convenient, and enjoyable for all. Hop on board with
                Appni Gaadi and experience a new era of transportation
                excellence.
            </AboutSection>
            <TextBanner>Our Product</TextBanner>
            <AboutSection
                image={image7}
                imageProps={{
                    w: ["8xl", "9xl"],
                }}
                reverse
            >
                <Heading>Apni-Gaadi User</Heading>
                The ApniGaadi User app is your gateway to convenient and
                reliable transportation. With its intuitive interface, you can
                easily book rides, track your driver's location in real-time,
                make secure in-app payments, and access essential ride details.
                The user app also provides access to exclusive offers, loyalty
                rewards, and responsive customer support, ensuring a seamless
                and enjoyable experience every time you travel with ApniGaadi.
                Experience the convenience and reliability of our user app and
                enjoy hassle-free transportation at your fingertips.
            </AboutSection>
            <AboutSection
                image={image8}
                imageProps={{
                    w: ["8xl", "8xl"],
                }}
                reverse
            >
                <Heading>Apni-Gaadi Driver</Heading>
                The ApniGaadi Driver app is a powerful tool designed for our
                drivers. With its user-friendly interface and robust features,
                drivers can efficiently manage their rides, track earnings,
                receive trip requests, and access important information, all in
                one convenient app. It empowers our drivers to provide seamless
                and exceptional service to our passengers, ensuring a smooth and
                efficient transportation experience for all.
            </AboutSection>
        </>
    );
}

export default About;
