import { ArrowBack, ArrowRight } from "../assets/icons";
import {
    Box,
    Container,
    Heading,
    Icon,
    Stack,
    Text,
    useBreakpointValue,
} from "@chakra-ui/react";

import React from "react";
import Slider from "react-slick";

const settings = {
    dots: true,
    arrows: false,
    fade: true,
    infinite: true,
    autoplay: true,
    speed: 500,
    autoplaySpeed: 5000,
    slidesToShow: 1,
    slidesToScroll: 1,
    pauseOnHover: false,
};

export default function Carousel() {
    const [slider, setSlider] = React.useState(null);

    const top = useBreakpointValue({ base: "90%", md: "50%" });
    const side = useBreakpointValue({ base: "30%", md: "40px" });
    const isMobile = useBreakpointValue({ base: true, md: false });

    const desktopImages = [
        "https://images.unsplash.com/photo-1610886023290-6ba32b20e354?ixlib=rb-4.0.3&q=85&fm=jpg&crop=entropy&cs=srgb&dl=javygo-mnItwT1ogBM-unsplash.jpg",
        "https://unsplash.com/photos/8MrZ7iMg8xw/download?ixid=M3wxMjA3fDB8MXxzZWFyY2h8MzB8fGNhYnxlbnwwfDB8fHwxNjg3NzkwMTUzfDI&force=true",
        "https://images.unsplash.com/photo-1590005990709-05d91a926621?ixlib=rb-4.0.3&q=85&fm=jpg&crop=entropy&cs=srgb&dl=tetiana-shyshkina--A5BQ7d0nN0-unsplash.jpg",
    ];

    const mobileImages = [
        "https://unsplash.com/photos/Lc9TOaDnPjg/download?ixid=M3wxMjA3fDB8MXxzZWFyY2h8Mnx8Y2FifGVufDB8fHx8MTY4NzY2OTMyN3ww&force=true",
        "https://unsplash.com/photos/6rWCc5h7Y7o/download?ixid=M3wxMjA3fDB8MXxzZWFyY2h8MTB8fGNhYnxlbnwwfHx8fDE2ODc2OTM2OTB8Mg&force=true",
        "https://unsplash.com/photos/RT1WZNWfcCs/download?ixid=M3wxMjA3fDB8MXxzZWFyY2h8MjB8fGNhYnxlbnwwfHx8fDE2ODc2OTM2OTB8Mg&force=true",
    ];

    return (
        <Box
            position={"relative"}
            height={"100vh"}
            width={"full"}
            overflow={"hidden"}
        >
            <link
                rel="stylesheet"
                type="text/css"
                charSet="UTF-8"
                href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css"
            />
            <link
                rel="stylesheet"
                type="text/css"
                href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css"
            />
            <Icon
                aria-label="left-arrow"
                variant="ghost"
                position="absolute"
                left={side}
                top={top}
                fontSize="3xl"
                zIndex={2}
                onClick={() => slider?.slickPrev()}
                as={ArrowBack}
                color={"white"}
            ></Icon>
            <Icon
                aria-label="right-arrow"
                variant="ghost"
                position="absolute"
                right={side}
                top={top}
                fontSize="3xl"
                zIndex={2}
                onClick={() => slider?.slickNext()}
                as={ArrowRight}
                color={"white"}
            ></Icon>
            <Slider {...settings} ref={(slider) => setSlider(slider)}>
                {(isMobile ? mobileImages : desktopImages).map(
                    (image, index) => (
                        <Box
                            key={index}
                            height={"6xl"}
                            position="relative"
                            backgroundPosition="center"
                            backgroundRepeat="no-repeat"
                            backgroundSize="cover"
                            backgroundImage={`-webkit-cross-fade(
                            url("${image}"),
                            url(data:image/gif;base64,R0lGODlhAQABAIAAAAUEBAAAACwAAAAAAQABAAACAkQBADs=),
                            28%
                            );`}
                        >
                            <Container
                                size="container.lg"
                                height="100vh"
                                color={"white"}
                                position="absolute"
                                right={isMobile ? "auto" : "7%"}
                            >
                                <Stack
                                    spacing={6}
                                    w={"full"}
                                    maxW={"xl"}
                                    position={"absolute"}
                                    top={"30%"}
                                >
                                    <Heading
                                        fontSize={{
                                            base: "5xl",
                                            md: "5xl",
                                            lg: "7xl",
                                        }}
                                    >
                                        Apni Gaadi
                                    </Heading>
                                    <Text
                                        fontSize={{
                                            base: "3xl",
                                            lg: "4xl",
                                        }}
                                        lineHeight={"100%"}
                                        fontWeight="bold"
                                    >
                                        Apni Gaadi Bulao
                                        <br />
                                        Apne Ghar Jao
                                    </Text>
                                </Stack>
                            </Container>
                        </Box>
                    )
                )}
            </Slider>
        </Box>
    );
}
